import { transformResponse } from ".";
import { axiosInstance } from "./axios";
import { MessageType } from "@/shared";
import { socket } from "./socket-io";
import { notificationModule } from "../modules";
import {
  IServerResponse,
  ICreateClientArgs,
  IGetAllClientsSuccess,
  ICreateClientSuccess,
  IUpdateClientSuccess,
  IUpdateClientArgs,
  IAdminClient,
  IInstallOnClientSuccess,
  IDeleteOnClientSuccess
} from "@/shared";

socket.on("finishedInstall", function(response: string, os: string) {
  let message;
  if (os === "Windows") {
    if (response.indexOf("error") === -1 && response.indexOf("Error") === -1) {
      message = {
        messages: [
          {
            name: "api.success.client.install",
            type: MessageType.success
          }
        ],
        payload: {}
      };
    } else {
      message = {
        messages: [
          {
            name: "api.error.client.install",
            type: MessageType.error
          }
        ],
        payload: {}
      };
    }
    notificationModule.addFromApiResponse(message);
  } else if (os === "Linux") {
    console.log("resp", response);
    if (response.indexOf("Installation of Backup380 finished") !== -1) {
      message = {
        messages: [
          {
            name: "api.success.client.install",
            type: MessageType.success
          }
        ],
        payload: {}
      };
    } else {
      message = {
        messages: [
          {
            name: "api.error.client.install",
            type: MessageType.error
          }
        ],
        payload: {}
      };
    }
    notificationModule.addFromApiResponse(message);
    console.log("response", response);
  } else if (os === "MacOS") {
    console.log("resp", response);
    if (response.indexOf("Installation of Backup380 finished") !== -1) {
      message = {
        messages: [
          {
            name: "api.success.client.install",
            type: MessageType.success
          }
        ],
        payload: {}
      };
    } else {
      message = {
        messages: [
          {
            name: "api.error.client.install",
            type: MessageType.error
          }
        ],
        payload: {}
      };
    }
    notificationModule.addFromApiResponse(message);
    console.log("response", response);
  }
});
