export const addClient = async (client: ICreateClientArgs) => {
  const response = await axiosInstance.post<
    IServerResponse<ICreateClientSuccess>
  >(`/admin/client`, client);

  return transformResponse(response);
};

export const getClients = async () => {
  const response = await axiosInstance.get<
    IServerResponse<IGetAllClientsSuccess>
  >(`/admin/client`);
  return transformResponse(response);
};

export const updateClient = async (
  id: number,
  client: IUpdateClientArgs
): Promise<IServerResponse<IUpdateClientSuccess>> => {
  const response = await axiosInstance.put<
    IServerResponse<IUpdateClientSuccess>
  >(`/admin/client/${id}`, client);
  return transformResponse(response);
};

export const deleteClient = async (id: number): Promise<IServerResponse> => {
  const response = await axiosInstance.delete(`/admin/client/${id}`);
  return transformResponse(response);
};
export const installOnClient = async (
  client: IAdminClient
): Promise<IServerResponse> => {
  const response = await axiosInstance.post<
    IServerResponse<IInstallOnClientSuccess>
  >(`/admin/clientInstall`, client);

  return transformResponse(response);
};
export const deleteOnClient = async (
  client: IAdminClient
): Promise<IServerResponse> => {
  const response = await axiosInstance.post<
    IServerResponse<IDeleteOnClientSuccess>
  >(`/admin/clientDelete`, client);

  return transformResponse(response);
};
