import store from "@/store";
import { Module, VuexModule, Action, Mutation } from "vuex-module-decorators";
import Vue from "vue";
import { AxiosResponse, AxiosError } from "axios";
import * as api from "../api";
import { Overwrite, RepoType } from "@/shared";
import { authModule } from ".";
@Module({ dynamic: true, store, name: "repositories", namespaced: true })
export default class RepositoriesModule extends VuexModule {
  private repositories: IRepository[] = [];

  @Action
  async fetchAllRepositories() {
    const response = await api.getRepos(authModule.userId);
    if (response.payload) {
      this.setRepositories(
        response.payload.repo.map(
          r =>
            ({
              autoUnlockDelay: r.autoUnlock,
              id: r.id,
              location: r.repoLocation,
              name: r.repoName,
              password: r.repoPassword,
              s3AccessKey: r.accessKey,
              s3SecretKey: r.secretAccessKey,
              type: r.repoType
            } as IRepository)
        )
      );
    }
  }

  @Action
  async createRepository(
    repository: Overwrite<
      IRepository,
      {
        id?: IRepository["id"];
      }
    > // TODO: maybe make more fields optional
  ) {
    const resp = await api.createRepo(authModule.userId, {
      accessKey: repository.s3AccessKey,
      repoLocation: repository.location,
      repoName: repository.name,
      repoPassword: repository.password,
      secretAccessKey: repository.s3SecretKey,
      repoType: repository.type,
      autoUnlock: repository.autoUnlockDelay
    });
    if (resp.payload) {
      const respRepo = resp.payload.repo;
      this.setRepositories([
        ...this.allRepositories,
        {
          autoUnlockDelay: respRepo.autoUnlock,
          id: respRepo.id,
          location: respRepo.repoLocation,
          name: respRepo.repoName,
          password: respRepo.repoPassword,
          s3AccessKey: respRepo.accessKey,
          s3SecretKey: respRepo.secretAccessKey,
          type: respRepo.repoType
        }
      ]);
      return resp;
    }
  }

  @Action
  async updateRepository(repository: IRepository) {
    const resp = await api.updateRepo(authModule.userId, repository.id, {
      accessKey: repository.s3AccessKey,
      repoLocation: repository.location,
      repoName: repository.name,
      repoPassword: repository.password,
      secretAccessKey: repository.s3SecretKey,
      repoType: repository.type,
      autoUnlock: repository.autoUnlockDelay
    });
    if (resp.payload) {
      this.setRepositories(
        this.allRepositories.map(r => (r.id === repository.id ? repository : r))
      );
      return resp;
    }
  }

  @Action
  async deleteRepository(id: number) {
    const resp = await api.deleteRepo(authModule.userId, id);
    const newRepos = this.allRepositories.filter(r => r.id !== id);
    this.setRepositories(newRepos);
    return resp;
  }

  @Mutation
  setRepositories(repos: IRepository[]) {
    this.repositories = repos;
  }

  get recentlyUsed() {
    // return at most 5 repositories (shown in side menu)
    return this.repositories.slice(0, 5);
  }
  get allRepositories() {
    return this.repositories;
  }

  get getById() {
    return (id: string | number) => {
      const num = Number(id);
      const repo = this.allRepositories.find(r => Number(r.id) === num);
      return repo;
    };
  }
}

export interface IRepository {
  name: string;
  password: string;
  location: string;
  s3AccessKey: string;
  s3SecretKey: string;
  id: number;
  type: RepoType;
  autoUnlockDelay: number;
}
