import { transformResponse } from ".";
import { IServerResponse } from "@/shared";
import { axiosInstance } from "./axios";

// TODO: @Pallinger use these interfaces

enum LogLevel {
  info = "info",
  warning = "warning",
  error = "error",
  success = "success"
}

export enum LogType {
  other = "other",
  client = "client",
  repo = "repo",
  job = "job"
}

type LogsArgs = {
  limit: number;
  offset?: number;
} & (
  | { type: LogType.other }
  | { clientId: number; type: LogType.client }
  | { repoId: number; type: LogType.repo }
  | { jobId: number; type: LogType.job });

interface ILogFromApiCommon {
  id: number;
  level: LogLevel;
  /**
   * Will be translated using `vue-i18n`
   */
  message: string;
  date: number;
}

export type LogFromApi = ILogFromApiCommon &
  (
    | { type: LogType.other }
    | { clientId: number; type: LogType.client }
    | { repoId: number; type: LogType.repo }
    | { jobId: number; repoId: number; type: LogType.job });

interface ILogsResponse {
  /**
   * Ordered by id descending
   */
  logs: LogFromApi[];
}

export const getLogDetailsUrl = (logId: number | string) =>
  `/system/log/${logId}`;

export const getLogDetails = async (logId: number): Promise<string> => {
  const response = await axiosInstance.get<string>(getLogDetailsUrl(logId), {
    responseType: "text"
  });
  return response.data;
};

export const getLogs = async (
  args: LogsArgs
): Promise<IServerResponse<ILogsResponse>> => {
  const response = await axiosInstance.get<IServerResponse<ILogsResponse>>(
    `/system/log`,
    {
      params: args
    }
  );
  return transformResponse(response);
};
